import React, { useEffect, useState } from "react";
import "./App.css";

import { FormControl, InputLabel, Input, IconButton } from "@mui/material";
import Message from "./Message";
import db from "./firebase";
import FlipMove from "react-flip-move";
import SendIcon from "@mui/icons-material/Send";

import {
  addDoc,
  collection,
  onSnapshot,
  orderBy,
  serverTimestamp,
} from "firebase/firestore";

function App() {
  const [input, setinput] = useState("");
  const [message, setmessage] = useState([]);
  const [username, setusername] = useState("");

  // useEffect(() => {
  //   const msg = collection(db, "messages");
  //   getDocs(msg).then((res) => {
  //     setmessage(res.docs.map((doc) => doc.data()));
  //   });
  // }, []);

  useEffect(() => {
    onSnapshot(
      collection(db, "messages"),
      orderBy("timestamp", "desc"),
      (snapshot) => {
        setmessage(
          snapshot.docs.map((doc) => {
            return { id: doc.id, data: doc.data() };
          })
        );
      }
    );
  }, []);

  useEffect(() => {
    setusername(prompt("Please Enter Your Name:"));
  }, []);

  const sendMessage = (event) => {
    event.preventDefault();
    const dbref = collection(db, "messages");
    addDoc(dbref, {
      username: username,
      text: input,
      timestamp: serverTimestamp(),
    });

    // setmessage([...message, { username: username, text: input }]);
    setinput("");
  };

  return (
    <div className="App">
      <img src="/image/messenger.png" alt="img_massage" />
      <h1>Facebook Messanger 👍</h1>
      <h2>Welcome {username}</h2>
      <form onSubmit={sendMessage} className="form_style">
        <FormControl className="form__control">
          <InputLabel>Enter a message...</InputLabel>
          <Input
            className="form__input"
            placeholder="Enter a message..."
            value={input}
            onChange={(event) => setinput(event.target.value)}
          />
          <IconButton
            className="form__icon"
            disabled={!input}
            type="button"
            variant="contained"
            color="primary"
          >
            <SendIcon />
          </IconButton>
        </FormControl>
      </form>
      <FlipMove>
        {message.map(({ data, id }) => {
          return <Message key={id} username={username} messages={data} />;
        })}
      </FlipMove>
    </div>
  );
}

export default App;
