import React, { forwardRef } from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import Typography from "@mui/material/Typography";
import "./Message.css";

const Message = forwardRef(({ username, messages }, ref) => {
  const isUser = username === messages.username;

  return (
    <div ref={ref} className={`messageui ${isUser && "message_user"}`}>
      <Card className={isUser ? "message_userCard" : "message_guestCard"}>
        <CardContent>
          <Typography color="black" variant="h5" component="h2">
            {!isUser && `${messages.username || "unknown user"}: `}
            {messages.text}
          </Typography>
        </CardContent>
        <CardActions></CardActions>
      </Card>
    </div>
  );
});

export default Message;
